import React from 'react'
import styled from 'styled-components';
import { useApp } from '../../context/Context';
import textContext from '../../textContext';
import { butterfly, inv1, inv2, inv3, inv4, inv5, inv6, inv7, inv8, inv9 } from '../../assets/images';

const Title = styled('p')`
	position: relative;
	&:before {
		position: absolute;
		content: '';
		top: -25px;
		left: 175px;
		height: 125%;
		width: 100%;
		background-image: url(${butterfly});
		background-size: contain;
		background-repeat: no-repeat;
		background-position: right;
	}
	@media (max-width: 600px) {
		&:before {
			left: 0px;
		}
	}
`;

export default function Partner() {

	const { language } = useApp();
  const text = textContext[language].partners;

  const sponsors = [
  	{ img: inv4 },
  	{ img: inv6 },
  	{ img: inv5 },
  	{ img: inv2 },
  	{ img: inv3 },
  	{ img: inv1 },
  	{ img: inv7 },
  	{ img: inv8 },
  	{ img: inv9 },
  	{ text: 'Do Kwon' },
  	{ text: 'Richard Dai' },
  	// { text: 'SJ Park' },
  	{ text: 'Ashwin Ramachandran', class: 'small' },
  ]

	return (
		<div id="partners">
			<div className="padding textContainer">
				<Title className="textHeader">{text.title}</Title>
				<div className="sponsorClass">
					{
						sponsors.map((l,i) => (
							<div key={i}>
								{l.img && <img src={l.img} alt="sponsor" /> }
								{l.text && <p className={l.class ? 'textSmall' : ''}>{l.text}</p> }
							</div>
						))
					}
				</div>
			</div>
		</div>
	)
};